import { FuseUtils } from '@fuse/utils';
import { infoPerfilInterface } from 'app/concomsis/jmy/interfaces/services';

export class ProveedorDatos
{
    id:string
    nombre:string
    telefonos:string[]
    email:string[]
    puesto:string
    fecha:{
        usuario:infoPerfilInterface
        fecha:string
    }[]
    constructor(datos:any){
        this.id= datos.id || FuseUtils.generateGUID();
        this.nombre= datos.nombre || '';
        this.telefonos= datos.telefonos || [];
        this.email= datos.email || [];
        this.fecha= datos.fecha ||[];
        this.puesto= datos.puesto || '';
    }
}
export class Proveedor
{
    id: string;
    name: string;
    rfc: string;
    calle: string;
    colonia: string;
    ciudad: string;
    cp: string;
    estado: string;
    contactos:ProveedorDatos[]
    contactocompra: string;
    contactopago: string;
    telefono: string;
    fechaalta: string;
    tipoProducto: string;
    avatar: string;
    nickname: string;
    company: string;
    jobTitle: string;
    email: string;
    phone: string;
    address: string;
    birthday: string;
    notes: string;

    /**
     * Constructor
     *
     * @param contact
     */
    constructor(contact)
    {
        {
            this.id = contact.id || FuseUtils.generateGUID();
            this.name = contact.name || '';
            this.rfc = contact.rfc || '';
            this.calle = contact.calle || '';
            this.colonia = contact.colonia || '';
            this.ciudad = contact.ciudad || '';
            this.cp = contact.cp || '';
            this.estado = contact.estado || '';
            this.contactos = contact.contactos || [];
            this.contactocompra = contact.contactocompra || '';
            this.contactopago = contact.contactopago || '';
            this.telefono = contact.telefono || '';
            this.fechaalta = contact.fechaalta || '';
            this.tipoProducto = contact.tipoProducto || '';
            this.avatar = contact.avatar || 'assets/images/avatars/proveedor.jpg';
            this.nickname = contact.nickname || '';
            this.company = contact.company || '';
            this.jobTitle = contact.jobTitle || '';
            this.email = contact.email || '';
            this.phone = contact.phone || '';
            this.address = contact.address || '';
            this.birthday = contact.birthday || '';
            this.notes = contact.notes || '';
        }
    }
}
